const Challenge = React.lazy(() =>
  import(/* webpackChunkName: "profile-default" */ './Challenges')
);

const Challenges = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Redirect exact from={`${match.url}/`} to={`${match.url}/view`} />
      <Route
        path={`${match.url}/view`}
        render={(props) => <Challenge {...props} />}
      />
       <Route
        path={`${match.url}/Add-Challenges`}
        render={(props) => <Layout {...props} />}
      />
     {/* <Route
        path={`${match.url}/education`}
        render={(props) => <Education {...props} />}
      />
      <Route
        path={`${match.url}/experience`}
        render={(props) => <Experience {...props} />}
      />
      <Route
        path={`${match.url}/links`}
        render={(props) => <Links {...props} />}
      />
      <Route
        path={`${match.url}/contact`}
        render={(props) => <Contact {...props} />}
      /> */}
      <Redirect to="/error" />
    </Switch>
  </Suspense>
);

export default Challenges;
